import Vue from 'vue';
import Vuex, { StoreOptions, MutationTree, GetterTree, ActionTree } from 'vuex';
//import { RootState } from './types';
import standardMenu from './StandardMenu';
Vue.use(Vuex);
 interface RootState {
    drawer: boolean;
    menuItems: Array<IMenuItem>;
}

interface IMenuItem {
    icon: string;
    text: string;
    route_name: string;
    children: IMenuItem[];
    [key: number]: string;
}
class MenuItem implements IMenuItem{
    icon: string="";
    text: string = "";
    route_name: string = "";
    children: IMenuItem[] = [];
    [key: number]: string;

}
let state : RootState= {
    drawer: true,
    menuItems: Array<IMenuItem>()
}
const getters: GetterTree<RootState,RootState> = {
    drawer(state): boolean {
        return state.drawer;
    },
    menuItems(state): Array<IMenuItem> {
        return state.menuItems;
    }
}

const mutations: MutationTree<RootState> = {
    drawerChange(state, value: boolean) {
        state.drawer = value;
    },
    setMenuItems(state, menuItem: Array<IMenuItem>) {
        state.menuItems = menuItem;
    },
    addMenuItem(state, menuItem:IMenuItem,parent?:IMenuItem) {
        if (parent == undefined)
            state.menuItems.push(menuItem);
        else
            parent.children.push(menuItem);
    }
}

const actions: ActionTree<RootState, RootState> = {
    drawerChange({ commit },value): any {
        commit('drawerChange', value);
    },
    addMenuItem({ commit }, menuItem: IMenuItem, parent?: IMenuItem) {
        commit('addMenuItem', { menuItem: menuItem, parent: parent });
    },
    fetchMenu({ commit }) {
        commit('setMenuItems',standardMenu)
    }
}
const store: StoreOptions<RootState> = {
    state: state,
    mutations: mutations,
    getters: getters,
    actions:actions
    
}

export default new Vuex.Store<RootState>(store);